import React from 'react';
import Table from 'react-bootstrap/Table';

const CustomerDocsTable = () => {
    return(
        <Table>
            <thead>
                <tcell>Document Types</tcell>
                <tcell>Customer 1</tcell>
                <tcell>Customer 2</tcell>
                <tcell>Customer 3</tcell>
            </thead>
            <tbody>
                <trow>
                    <tcell>Document A</tcell>
                    <tcell>Document 1-A</tcell>
                    <tcell>Document 2-A</tcell>
                    <tcell>Document 3-A</tcell>
                </trow>
                <trow>
                    <tcell>Document B</tcell>
                    <tcell>Document 1-B</tcell>
                    <tcell>Document 2-B</tcell>
                    <tcell>Document 3-B</tcell>
                </trow>
            </tbody>
        </Table>
    )
}

export default CustomerDocsTable;